import { commumal } from "../public";

var reimAudited={
    passValue:"",
    methods:{
        init(){
            $(".submit").on("click",reimAudited.events.submit);
            $(".btnBack").on("click",commumal.back)
        }
    },
    events:{
        filldata(param){
             param.SP_NAME = 'SP_RBRB_REIMBURSE_INFO_BATCH_SELECT';
             param.m='multi_result';
             $.defs.loadingshow()
             $.defs.myPost(param).then((data)=>{
                 $.defs.loadinghide()
                 if(data.result.RETURN_CODE == 0){
                      let desc=reimAudited.events.DESC(data.rbrb_info,data.rbrb_auditing);
                      let arr=reimAudited.events.estimate(desc);
                      console.log(arr)
                      $('.queryContent').html(template('AllViewTemplate', arr));
                     let li=$(".queryContent .itemQ");
                     if (data.rbrb_images.length != 0) {
                        $.each(data.rbrb_images, function (i, v) {
                            $(li).each(function (index, value) {
                                if (v.RBRB_KY == $(value).attr('rbrb_ky')) {
                                    var tpl = "<li class=\"weui-uploader__file\"  rbim_ky=\"{{RBIM_KY}}\"  rbrb_ky=\"{{RBRB_KY}}\" style=\"background-image: url('../file/Transmit?fileName={{RBIM_NAME_PATH}}')\"></li>";
                                    tpl = template.render(tpl, {
                                        RBIM_KY: v.RBIM_KY,
                                        RBRB_KY: v.RBRB_KY,
                                        RBIM_NAME_PATH: commumal.urlencode(v.RBIM_NAME_PATH)
                                    });
                                    $(value).find(".uploaderCustomFiles").append(tpl);
                                }
                            });
                        });
                        li.find('.uploaderCustomFiles').on('click',reimAudited.config.onShowImg);
                    }

                 }else{
                     history.go(-1)
                 }
             })
             
        },
        DESC(info, audit){
            $.each(info, function(i, v) {
                var  desc = [];
                $.each(audit, function(index, value) {
                    if (v.RBRB_KY == value.RBRB_KY) {
                        desc.push(value);
                    }
                });
                if (desc.length != 0) {
                    v.DESC = desc;
                }
            });
            return info
        },
        estimate(data) {
            for(var i=0;i<data.length;i++){
                if(data[i].FORMULA!=""){
                    var rc1=template.render(data[i].FORMULA,{
                        LIMIT_AC:data[i].LIMIT_STAY_AMT,
                        LIMIT_BH:data[i].LIMIT_ENTT_AMT,
                        LIMIT_TM:data[i].LIMIT_FOOD_AMT,
                        ATDC_DAY:data[i].ATDC_DAY,
                        ATDC_HUMAN_CNT:data[i].ATDC_HUMAN_CNT
                    })
                    var  preturn=eval(rc1);
                    data[i].cost=preturn
                }else{
                    data[i].cost=""
                }
            }
            return data
        },
        submit(){
            let li=$(".queryContent .itemQ"),
            SubmitParam = [];
            $(li).each(function (i, v) {
                var val = $(v).find('.passCheckbox input[type="radio"]:checked').val();
                if (val != 'D') {
                    var param = $.defs.submit({
                        container: $(v)
                    });
                    param.RBRB_KY = $(v).attr('RBRB_KY');
                    param.SP_NAME = 'SP_RBRB_REIMBURSE_INFO_STATUS_UPDATE';
                    param.m = 'UPDATE';
                    param.REVIEW_IND = val;
                    SubmitParam.push(param);
                }
            });
            var arr=SubmitParam.filter(function (item) {
                return item.REVIEW_IND=="D"
           });
           if(arr.length==SubmitParam.length){
               commumal.confirm({
                   title: '您的审核状态都为待定，是否确认？',
                   click:()=>{
                       history.go(-1)
                   }
               })
           }else{
               commumal.confirm({
                   click:()=>{
                       reimAudited.events.submitEnd(SubmitParam)
                   }
               })
           }

        },
        submitEnd(SubmitParam){
            $.defs.loadingshow();
            $.defs.myPost({
                m: 'batch_update_transation',
                TB_NAME: 'fdsfd',
                SP_NAME:"sdfd",
                DETAIL_STRING: JSON.stringify(SubmitParam)
            }).then((data)=>{
                 $.defs.loadinghide();
                 if(data.RETURN_CODE == 0){
                    weui.toast('操作成功', 2500);
                    setTimeout(()=>{
                         history.go(-1)
                    },2600)

                 }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                 }
            })
        }

    },
    config:{
        init(){
            $(".top .huan").text("待审核>报销管理");
            template.defaults.escape=false
            let param=spspa.getMessage("auditing");
             reimAudited.passValue=param;
             spspa.clearMessage();
             if(commumal.isEmptyObj(param)){
                  history.go(-1)
             }else{
                 reimAudited.events.filldata(param.content)
             }
        },
        onShowImg(e) {
            var $target = $(e.target);
            while (!$target.hasClass('weui-uploader__file') && $target) {
                $target = $target.parent();
            }
            if (!$target.length) return;

            var url = $target.attr('style') || '';
            var id = $target.data('id');

            if (url) {
                url = url.match(/url\((.*?)\)/)[1].replace(/"/g, '');
            }
            var gallery = weui.gallery(url, {
                className: 'deleted'
               
            });
        },
    }
}
$(function(){
    reimAudited.config.init();
    reimAudited.methods.init()
})


